import React from "react";
import { Switch, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Login from '../component/Login/index';
import Registration from "../component/Registration/index";
import Dashboard from '../component/Dashboard/index';
import Homepage from '../component/Homepage/index';
import Megapod from '../component/Megapod/index';
import Miner from '../component/Miner/index';
import Dataex from '../component/Dataex/index';

const BaseRouter = (...props) => (
  <Switch>

    <Route exact path="/signup/" component={Registration} />
    <Route exact path="/" component={Login} />      
    <PrivateRoute exact path="/dashboard" component={ Homepage } />
    <PrivateRoute  path="/dashboard1" component={ Dashboard } />
    <PrivateRoute  path="/dataexport" component={ Dataex } />
    <PrivateRoute  path="/megapod" component={ Megapod } />
    <PrivateRoute  path="/miner" component={ Miner } />

   

    
</Switch>
);

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      props.isAuthenticated ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: "/"
          }}
        />
      )
    }
  />
);


const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.isAuthenticated
  }
}

export default connect(mapStateToProps)(BaseRouter);
